@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,700;1,400&display=swap);
.post{background-color:#fadfe3;color:grey;text-transform:capitalize;width:100%;text-align:left;display:flex;justify-content:flex-start;align-items:center;padding-left:20px;border:none}.post:hover{background-color:#bd6d7a;color:white;cursor:pointer;transition:0.1s}.post:focus{outline:none}

body{font-family:'Montserrat', sans-serif}.heading1{font-size:30px;margin-bottom:40px;text-transform:capitalize}.heading2{font-size:20px;margin:10px 0;font-weight:400}.heading3{font-size:18px;margin:10px 0}.paragraph{font-weight:400;line-height:30px}.single-line-text{font-weight:700;line-height:15px;font-size:12px;display:block}

.image-wrapper{position:relative;width:100%}.image{position:absolute;left:0;top:0;width:100%;height:100%}

.input{padding:5px 15px;background-color:white;color:black;border-radius:10px;border:none;margin:0 10px;font-family:'Montserrat', sans-serif;flex-grow:1;margin:5px 0}.input:focus{outline:none;box-shadow:0 3px 10px 2px #2e2e2e}.button-submit{padding:5px 15px;background-color:white;color:black;border-radius:10px;border:none;cursor:pointer;font-family:'Montserrat', sans-serif;flex-grow:0;margin:5px 10px}.button-submit:focus{outline:none}.button-submit:hover{box-shadow:0 3px 10px 2px #2e2e2e}.input-wrapper{display:flex;width:100%;margin:20px 0;flex-wrap:wrap;align-self:flex-end;bottom:30px}

.wrapper{display:flex;flex-direction:column;min-height:300px;justify-content:space-between;padding-top:30px}

.nav{display:flex;justify-content:flex-end;margin:40px 0}.nav__item{padding:0 20px 0 0;text-decoration:none;color:#bd6d7a;transition:0.2s;font-weight:700}.nav__item:hover{color:grey}

.pop-up-wrapper{background-color:#e6fffc;position:relative;padding:50px;display:flex;justify-content:center;align-items:center}.close-btn{background-color:transparent;position:absolute;top:10px;right:10px;border:none;cursor:pointer;font-weight:bold;font-size:20px}.pop-up{position:absolute;display:flex;justify-content:center;align-items:center;top:0;left:0;width:100%;height:100vh;background-color:rgba(255,255,255,0.5);z-index:1}

body{
    background-color: rgb(250, 223, 227);
}
